<template>
  <div class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>注册新用户
        <span class="go">我有账号，去 <a href="login.html" target="_blank">登录</a>
        </span>
      </h3>
      <!-- 手机号 -->
			<div class="content">
				<label>手机号:</label>
				<input 
					type="text" 
					placeholder="请输入你的手机号" 
					v-model="phone"
					v-validate="'required|phone_rule'" 
					name="手机"
				/>
				<span class="error-msg">{{ errors.first('手机') }}</span>
			</div>

			<!-- 验证码 -->
			<div class="content">
				<label>验证码:</label>
				<input 
					type="text" 
					placeholder="请输入验证码" 
					v-model="code" 
					v-validate="'required|code_rule'" 
					name="验证码"
				/>
				<button class="getcode" @click="getCode">获取验证码</button>
				<span class="error-msg">{{ errors.first('验证码') }}</span>
			</div>

			<!-- 密码 -->
			<div class="content">
				<label>登录密码:</label>
				<input
					type="text"
					placeholder="请输入你的登录密码"
					v-model="password"
					v-validate="{required:true,pwd_rule:true}" 
					name="密码"
				/>
				<span class="error-msg">{{ errors.first('密码') }}</span>
			</div>

			<!-- 确认密码 -->
			<div class="content">
				<label>确认密码:</label>
				<input 
					type="text" 
					placeholder="请输入确认密码" 
					v-model="re_password" 
					v-validate="{required:true,ispwd:password}" 
					name="重复密码"
				/>
				<span class="error-msg">{{ errors.first('重复密码') }}</span>
			</div>

			<!-- 协议 -->
			<div class="controls">
				<input 
					type="checkbox" 
					v-model="agree" 
					v-validate="{isagree:true}" 
					name="协议"
				/>
				<span>同意协议并注册《尚品汇用户协议》</span>
				<span class="error-msg">{{ errors.first('协议') }}</span>
			</div>
			
      <div class="btn">
        <button @click="handleRegister">完成注册</button>
      </div>
    </div>

    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
        <li>商家入驻</li>
        <li>营销中心</li>
        <li>手机尚品汇</li>
        <li>销售联盟</li>
        <li>尚品汇社区</li>
      </ul>
      <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
      <div class="beian">京ICP备19006430号
      </div>
    </div>
  </div>
</template>

<script>
	import {reqSendCode,reqRegister} from '@/api'

  export default {
    name: 'Register',
		data() {
			return {
				phone:'', //手机号
				password:'',//密码
				code:'',//验证码
				re_password:'',//重复密码
				agree:false //是否同意协议
			}
		},
		methods:{
			// 获取验证码的回调
			async getCode(){
				// 请求服务器获取验证码
				let result = await reqSendCode(this.phone)
				// 判断业务逻辑是否成功
				if(result.code === 200){
					// 若成功，提示一下
					this.$message.success('验证码获取成功！')
					// 直接把验证码填好
					this.code = result.data
				}else{
					this.$message.warning(result.message)
				}
			},
			// 完成注册按钮的回调
			async handleRegister(){
				//验证所有输入项
				let result = await this.$validator.validateAll()
				if(result){
					// 获取用户输入
					const {phone,code,password} = this
					// 请求注册(注意此处传递的参数是一个对象)
					let result = await reqRegister({phone,code,password})
					// 判断业务逻辑是否成功
					if(result.code === 200){
						// 若业务逻辑成功，提示一下
						this.$message.success('注册成功！')
						// 跳转到登录
						this.$router.push('/login')
					}else{
						// 若业务逻辑失败，提示原因
						this.$message.warning(result.message)
					}
				}
			}
		}
  }
</script>

<style lang="less" scoped>
  .register-container {
    .register {
      width: 1200px;
      height: 445px;
      border: 1px solid rgb(223, 223, 223);
      margin: 0 auto;

      h3 {
        background: #ececec;
        margin: 0;
        padding: 6px 15px;
        color: #333;
        border-bottom: 1px solid #dfdfdf;
        font-size: 20.04px;
        line-height: 30.06px;

        span {
          font-size: 14px;
          float: right;

          a {
            color: #e1251b;
          }
        }
      }

      div:nth-of-type(1) {
        margin-top: 40px;
      }

      .content {
        padding-left: 390px;
        margin-bottom: 18px;
        position: relative;

        label {
          font-size: 14px;
          width: 96px;
          text-align: right;
          display: inline-block;
        }

        input {
          width: 270px;
          height: 38px;
          padding-left: 8px;
          box-sizing: border-box;
          margin-left: 5px;
          outline: none;
          border: 1px solid #999;
        }

        img {
          vertical-align: sub;
        }

        .error-msg {
          position: absolute;
          top: 100%;
          left: 495px;
          color: red;
        }
      }

      .controls {
        text-align: center;
        position: relative;

        input {
          vertical-align: middle;
        }

        .error-msg {
          position: absolute;
          top: 100%;
          left: 495px;
          color: red;
        }
      }

      .btn {
        text-align: center;
        line-height: 36px;
        margin: 17px 0 0 55px;

        button {
          outline: none;
          width: 270px;
          height: 36px;
          background: #e1251b;
          color: #fff !important;
          display: inline-block;
          font-size: 16px;
        }
      }

			.getcode{
				height: 38px;
				margin-left: 10px;
				padding: 5px;
			}
    }

    .copyright {
      width: 1200px;
      margin: 0 auto;
      text-align: center;
      line-height: 24px;

      ul {
        li {
          display: inline-block;
          border-right: 1px solid #e4e4e4;
          padding: 0 20px;
          margin: 15px 0;
        }
      }
    }
  }
</style>